




































import ProductListComponent from '@/app/website/client/view/component/ProductList.vue';
import {
  computed, defineComponent, onMounted, onUnmounted, PropType, ref,
} from '@vue/composition-api';
import {
  StyleKeyValue,
  tileStyleSelector,
  generalStyles,
  textStyles,
  imageOverlayStyles,
  actionLinkStyles,
} from '@/app/website/client/logic/tile-style';
import { checkIfTileTransparent } from '@/app/website/client/logic/tile-properties';
import {
  StoreTileContent,
  StoreTileDesign,
  StoreTileExternalContent,
  StoreTileLayout,
} from '@/app/shared/client/type/tile/store-tile';
import { ViewportSettings } from '@/app/shared/client/type/settings/viewport-settings';
import {
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import CenterDefaults from '@/app/website/client/defaults/store/center.json';
import { StoreTileLayoutDefaults } from '@/app/shared/client/type/defaults/store-tile-defaults';
import { checkIfTileShown } from '@/app/website/client/logic/appearance-behaviour';
import { useWindowScrollSettings } from '@/app/shared/client/logic/scroll-settings';
import { useTileOffsetSettings } from '@/app/shared/client/logic/offset-settings';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import { ImageSettings } from '@/app/shared/client/type/settings/image-settings';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import { ActionLinkSettings } from '@/app/shared/client/type/settings/action-link-settings';
import FontLoader from '@/app/website/client/view/component/FontLoader.vue';
import { Profile } from '@/app/shared/client/type/profile';
import TileTitle from '@/app/website/client/view/component/TileTitle.vue';
import GridPlaceholderComponent from '@/app/website/client/view/component/GridPlaceholder.vue';
import useI18N from '@/app/shared/client/logic/i18n';

interface FeaturedProductsTileProps {
  tileId: string;
  profile: Profile;
  content: StoreTileContent;
  externalContent: StoreTileExternalContent;
  design: StoreTileDesign;
  viewportSettings: ViewportSettings;
}

function getDefaults(layout: StoreTileLayout | undefined): StoreTileLayoutDefaults | undefined {
  switch (layout) {
    case 'CENTER':
      return CenterDefaults as StoreTileLayoutDefaults;
    default:
      return undefined;
  }
}

function tileStyles(design: StoreTileDesign): StyleKeyValue[] {
  const styles: StyleKeyValue[] = [
    ...generalStyles(design.general),
    ...textStyles('headline', design.productListTitle),
    ...textStyles('product-name', design.productName),
    ...textStyles('product-price', design.productPrice),
    ...imageOverlayStyles('product-image', design.productImage),
    ...actionLinkStyles('button', design.buyNowButton),
  ];

  return styles;
}

const FeaturedProductsTile = defineComponent({
  components: {
    GridPlaceholderComponent,
    ProductListComponent,
    FontLoader,
    TileTitle,
  },
  props: {
    profile: {
      required: true,
      type: Object as PropType<Profile>,
    },
    content: {
      required: true,
      type: Object as PropType<StoreTileContent>,
    },
    externalContent: {
      required: true,
      type: Object as PropType<StoreTileExternalContent>,
    },
    design: {
      required: true,
      type: Object as PropType<StoreTileDesign>,
    },
    viewportSettings: {
      required: true,
      type: Object as PropType<ViewportSettings>,
    },
    isHeadTile: {
      required: true,
      type: Boolean,
    },
  },
  setup(props: FeaturedProductsTileProps, { attrs, root, emit }) {
    const i18n = useI18N(root.$store);
    const tileRef = ref<HTMLElement>();

    const {
      windowScrollTop,
      subscribeOnWindowScrollChange,
    } = useWindowScrollSettings();

    const {
      tileOffsetTop,
      tileOffsetHeight,
      subscribeOnTileOffsetChange,
    } = useTileOffsetSettings(tileRef);

    const defaults = computed(() => getDefaults(props.design.layout));

    const design = computed<StoreTileDesign>(() => {
      const userValue = props.design;
      const general: GeneralDesignSettings = {
        ...defaults.value?.general,
        ...userValue.general,
        background: {
          ...defaults.value?.general?.background,
          ...userValue.general?.background,
          solid: {
            ...defaults.value?.general?.background?.solid,
            ...userValue.general?.background?.solid,
          },
          gradient: {
            ...defaults.value?.general?.background?.gradient,
            ...userValue.general?.background?.gradient,
          },
        },
      };

      const productListTitle: TextSettings = {
        ...(defaults.value?.productListTitle as TextSettings),
        ...userValue.productListTitle,
      };

      const productName: TextSettings = {
        ...(defaults.value?.productName as TextSettings),
        ...userValue.productName,
      };

      const productPrice: TextSettings = {
        ...(defaults.value?.productPrice as TextSettings),
        ...userValue.productPrice,
      };

      const productImage: ImageSettings = {
        ...defaults.value?.productImage,
        ...userValue.productImage,
        overlay: {
          ...defaults.value?.productImage?.overlay,
          ...userValue.productImage?.overlay,
          solid: {
            ...defaults.value?.productImage?.overlay?.solid,
            ...userValue.productImage?.overlay?.solid,
          },
          gradient: {
            ...defaults.value?.productImage?.overlay?.gradient,
            ...userValue.productImage?.overlay?.gradient,
          },
        },
      };

      const buyNowButton: ActionLinkSettings = {
        ...(defaults.value?.buyNowButton as ActionLinkSettings),
        ...userValue.buyNowButton,
      };

      return {
        layout: userValue.layout,
        productListTitle,
        productName,
        productPrice,
        productImage,
        buyNowButton,
        general,
      };
    });

    const isTileShowRef = ref(false);
    const isTileShown = computed(() => checkIfTileShown(
      isTileShowRef,
      props.viewportSettings,
      windowScrollTop.value,
      tileOffsetTop.value,
      tileOffsetHeight.value,
    ));

    const fonts = computed(() => [
      design.value.productListTitle?.font,
      design.value.productName?.font,
      design.value.productPrice?.font,
      design.value.buyNowButton?.font,
    ]);

    const styleSelector = computed(() => {
      const styles = tileStyles(design.value);
      return tileStyleSelector(`#${attrs.id}`, styles);
    });

    const storeClosed = computed(() => props.profile.storeClosed ?? false);

    const hasTransparentBackground = computed(() => checkIfTileTransparent(
      design.value?.general,
      root.$store.state.siteSettings?.design,
    ));

    const cpDesign = computed(() => props.externalContent.cpDesign);

    const hasHeadline = computed(() => {
      const isHeadlineVisible = design.value.productListTitle?.visible;
      const hasHeadlineContent = isNotNullOrEmpty(props.content.productListTitle);
      return isHeadlineVisible && hasHeadlineContent;
    });

    const hasProducts = computed(() => props.externalContent.storeData.products.length > 0);
    const productsContent = computed(() => ({
      productListTitle: props.content.productListTitle,
      buyNowButton: props.content.buyNowButton,
      onSaleLabel: props.content.onSaleLabel,
      outOfStockLabel: props.content.outOfStockLabel,
      soldOutLabel: props.content.soldOutLabel,
      products: props.externalContent.storeData.products,
    }));
    const productsDesign = computed(() => ({
      productListTitle: design.value.productListTitle,
      productName: design.value.productName,
      productPrice: design.value.productPrice,
      productImage: design.value.productImage,
      buyNowButton: design.value.buyNowButton,
    }));
    const productsCpDesign = computed(() => ({
      imageSize: cpDesign.value.productListImageSize,
      imageLayout: cpDesign.value.productListImageLayout,
      imagePosition: cpDesign.value.productListImagePosition,
      productInfoLayout: cpDesign.value.productListCardLayout,
      showCardFrame: cpDesign.value.productListShowCardFrame,
      showAdditionalImage: cpDesign.value.productListShowAdditionalImage,
      titleBehavior: cpDesign.value.productListTitleBehaviour,
      priceBehavior: cpDesign.value.productListPriceBehaviour,
      buyNowBehavior: cpDesign.value.productListBuyButtonBehaviour,
    }));

    const showTile = computed(() => !storeClosed.value && hasProducts.value);
    const isPreview = root.$store.getters.isPreviewMode;
    const showPlaceholder = computed(() => isPreview && !showTile.value);

    onMounted(() => {
      emit('tile-loaded');
      subscribeOnWindowScrollChange();
      subscribeOnTileOffsetChange();
    });

    onUnmounted(() => {
      emit('tile-unloaded');
    });

    return {
      i18n,
      tileRef,
      productsContent,
      productsDesign,
      productsCpDesign,
      fonts,
      styleSelector,
      hasHeadline,
      hasProducts,
      hasTransparentBackground,
      isTileShown,
      showTile,
      showPlaceholder,
    };
  },
});

export default FeaturedProductsTile;
